
import { mapGetters, mapActions } from 'vuex';
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


export default {
components: {
Swiper,
SwiperItem
},
name: 'wordSwiper',
props: ['notice', 'titlePic', 'moreUrl'],
data() {
return {
swiperheight: '40px'
};
},
methods: {
...mapActions([])
},
watch: {},
computed: {
...mapGetters([])
},
created() {
var swiperheight = 40 * document.body.clientWidth / 750 * 2;
this.swiperheight = swiperheight + 'px';
},
mounted() {}
};
